import React from "react";
import useState from "react";
import "./styles.css";

function App() {
  const [tekst, setTekst] = React.useState();
  const [tekst1, setTekst1] = React.useState([]);
  function unos(opis) {
    setTekst(opis.target.value);
  }
  function dodati() {
    setTekst1((prev) => {
      return [...prev, tekst];
    });
    setTekst("");
  }
  return (
    <div className="container">
      <div className="heading">
        <h1>To-do list</h1>
      </div>
      <div className="form">
        <input onChange={unos} type="text" value={tekst} />
        <button onClick={dodati}>
          <span>Add</span>
        </button>
      </div>
      <div>
        <ul>
          {tekst1.map((item) => (
            <li>{item}</li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default App;
